*{box-sizing:border-box}html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,font,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,caption{margin:0;padding:0;border:0;outline:0;font-size:100%;vertical-align:baseline;background:transparent;font-family:"Poppins", sans-serif}::-webkit-scrollbar{width:10px}::-webkit-scrollbar-track{background:#f1f1f1}::-webkit-scrollbar-thumb{background:#19181a;border-radius:12px}::-webkit-scrollbar-thumb:hover{background:#0f0f0f}

*{box-sizing:border-box}.home-wrapper{position:absolute;top:0;left:0;height:100%;width:100%;display:flex;justify-content:center;align-items:center}.home-square-one{position:absolute;top:22.5%;left:24%;width:28%;height:25%;background-color:#e6af2e;z-index:-1}.home-square-two{position:absolute;top:42.5%;left:44%;width:28%;height:25%;background-color:#dad2bc;z-index:-2}.home-name{letter-spacing:0.15em;white-space:nowrap;color:#191716;text-transform:uppercase;font-family:"Poppins", sans-serif;font-weight:bold;letter-spacing:0.2rem;font-size:4rem;padding:6px;margin:12px;display:flex;justify-content:center;align-items:center}@media screen and (max-width: 480px){.home-name{font-size:1.4rem}}.home-occupation{display:flex;justify-content:center;align-items:center}.home-description{display:flex;justify-content:center;align-items:center;text-align:center}@-webkit-keyframes typing{from{width:0}to{width:100%}}@keyframes typing{from{width:0}to{width:100%}}@-webkit-keyframes blink-caret{from,to{border-right:0.15em solid transparent}50%{border-right:0.15em solid #e6af2e}}@keyframes blink-caret{from,to{border-right:0.15em solid transparent}50%{border-right:0.15em solid #e6af2e}}

*{box-sizing:border-box}.interest-wrapper{padding:0;margin:0}.interest-name{font-family:"Poppins", sans-serif;color:#191716;position:relative;display:flex;justify-content:center;font-weight:bold;letter-spacing:0.2rem;font-size:1.8rem;padding:6px;margin:12px}@media screen and (max-width: 480px){h1{margin:6px}}.interest-menu{position:relative;display:flex;justify-content:center;margin:10px}.interest{-webkit-tap-highlight-color:transparent;-webkit-touch-callout:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;display:inline-block;height:40px;text-align:center;vertical-align:bottom;padding:3px;margin:3px;color:#191716}.interest:hover,.interest.active{color:#e6af2e}.interest-content{position:relative;margin:2.5px;top:50%;transform:translateY(-50%)}.interest-content h3{text-transform:uppercase;cursor:pointer}

.cards-wrapper{position:relative;display:flex;justify-content:center}.cards{text-align:center;max-width:80vw}

*{box-sizing:border-box}.card{-webkit-tap-highlight-color:transparent;-webkit-touch-callout:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;transition:all 0.4s cubic-bezier(0.175, 0.885, 0, 1);background-color:#fff;width:80vw;max-width:360px;height:80vw;max-height:220px;position:relative;overflow:hidden;box-shadow:0px 13px 10px -7px rgba(0,0,0,0.1);display:inline-block;margin:12.5px}.card:hover .card-info{z-index:10;position:absolute;padding:15px;background-color:rgba(0,0,0,0.7);height:100%;width:100%;top:0;left:0;text-align:center;display:flex;align-items:center;justify-content:center}@media screen and (max-width: 480px){.card{margin:12.5px 0}}.card-image{position:relative;object-fit:cover;background-position:center;background-repeat:no-repeat;width:100%;height:100%;top:0;z-index:1;display:block}.card-title{font-family:"Roboto Slab", serif;font-size:26px;letter-spacing:1.5px;font-weight:1000;color:rgba(255,255,255,0.9);margin:25px;background:transparent}

*{box-sizing:border-box}.contact-wrapper{position:absolute;top:0;left:0;height:100%;width:100%;display:flex;justify-content:center;align-items:center}.contact-square-one{position:absolute;top:22.5%;left:24%;width:28%;height:25%;background-color:#e6af2e;z-index:-1}.contact-square-two{position:absolute;top:42.5%;left:44%;width:28%;height:25%;background-color:#dad2bc;z-index:-2}.contact-title{position:relative;letter-spacing:0.15em;color:#191716;font-family:"Poppins", sans-serif;font-weight:bold;letter-spacing:0.2rem;font-size:2.4rem;padding:6px;margin:12px;text-align:center;display:block;padding-left:15%;padding-right:15%}@media screen and (max-width: 480px){.contact-title{font-size:1.1rem;padding-left:10%;padding-right:10%}}.contact-description{text-align:center;padding-left:10%;padding-right:10%}.contact-social-media-wrapped{display:flex;align-items:center;justify-content:center;margin:10px}.contact-icon{color:#191716;margin:10px;cursor:pointer}.contact-icon:hover{color:#146071}

.nav-wrapper{position:absolute;padding:0 2rem;margin:0;top:50%;right:0;z-index:10}.nav-squares-wrapper{position:relative;display:inline-block;top:50%;transform:translateY(-50%)}.nav-square-wrapper{margin:0;padding:0;max-height:100%}.nav-square-wrapper svg{fill:grey;stroke:grey;cursor:pointer;max-height:100%;max-width:100%}.nav-square-wrapper svg.active{fill:orange;stroke:orange}

*{box-sizing:border-box}.mobile-menu{position:absolute;z-index:1000;top:0;left:0;width:auto;height:auto}.mobile-menu.active{height:100%;width:100%;padding:0;margin:0;background:#191716;-webkit-animation:expand 0.5s ease 0s 1 normal forwards;animation:expand 0.5s ease 0s 1 normal forwards}@-webkit-keyframes expand{0%{height:30px;width:40px}100%{height:100%;width:100%}}@keyframes expand{0%{height:30px;width:40px}100%{height:100%;width:100%}}@-webkit-keyframes slideInFromLeft{0%{opacity:0}100%{opacity:1}}@keyframes slideInFromLeft{0%{opacity:0}100%{opacity:1}}.menu-items{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);padding:0;margin:0;opacity:0;-webkit-animation:slideInFromLeft 1s ease-out 0.3s 1 forwards;animation:slideInFromLeft 1s ease-out 0.3s 1 forwards;background:transparent}.menu-items .menu-item{-webkit-tap-highlight-color:transparent;-webkit-touch-callout:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;text-align:center;font-family:"Poppins", sans-serif;color:#e6af2e;position:relative;display:block;font-weight:bold;letter-spacing:0.2rem;font-size:1.4rem;cursor:pointer;text-transform:uppercase}.menu-items .menu-item:hover{color:#fff}.mobile-menu-hamburger{-webkit-tap-highlight-color:transparent;-webkit-touch-callout:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;width:40px;height:30px;position:relative;margin:18px;background:transparent;transform:rotate(0deg);transition:0.5s ease-in-out;cursor:pointer}.mobile-menu-hamburger span{display:block;position:absolute;height:5px;width:100%;background:#191716;border-radius:10px;opacity:1;left:0;transform:rotate(0deg);transition:0.25s ease-in-out}.mobile-menu-hamburger span:nth-child(1){top:0px}.mobile-menu-hamburger span:nth-child(2),.mobile-menu-hamburger span:nth-child(3){top:10px}.mobile-menu-hamburger span:nth-child(4){top:20px}.mobile-menu-hamburger.active span:nth-child(1){top:10px;width:0%;left:50%}.mobile-menu-hamburger.active span:nth-child(2){transform:rotate(45deg);background:#e6af2e}.mobile-menu-hamburger.active span:nth-child(3){transform:rotate(-45deg);background:#e6af2e}.mobile-menu-hamburger.active span:nth-child(4){top:10px;width:0%;left:50%}

